export default {
    props: {
        bill: {
            type: Object,
            default: {
                billId: '',
                billDate: '',
                billType: '' ,//订单类型，分销订单，普通订单
                billPrice: 0
            }
        }
    },
    methods: {
        clickBill(){
           this.$emit('click',{
               type:'title',
               detail:{
                   val:this.bill
               }
           })
        }
    },
}
